<template>
  <div class="search_result">
    <el-dialog
      title="咨询"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
    >
      <el-input v-model="contact_name" placeholder="姓名"></el-input>
      <el-input v-model="contact_number" placeholder="联系方式"></el-input>
      <el-input v-model="contact_note" placeholder="咨询内容"></el-input>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false"
          >确 定</el-button
        >
      </span>
    </el-dialog>

    <!-- 图片显示弹窗 -->
    <el-dialog :visible.sync="imgVisible">
      <el-image :src="imgUrl">
        <!-- :preview-src-list="scope.row.pictures"> -->
      </el-image>
    </el-dialog>

    <el-row>
      <el-col :span="4" :offset="0">
        <el-input
          placeholder="请输入型号"
          v-model="input"
          class="input-search-bar"
        >
          <template slot="append">
            <el-button
              icon="el-icon-search"
              @click="searchBtnClicked"
            ></el-button>
          </template>
        </el-input>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="12" :offset="0">
        <label style="float: left">搜索结果：</label>
      </el-col>
      <el-col :span="12" :offset="0" style="text-align: right">
        <el-badge :value="allNum" class="item">
          <i
            class="el-icon-shopping-cart-full icon_class"
            @click="openCart"
          ></i>
        </el-badge>
      </el-col>
    </el-row>
    <el-table
      :data="tableData"
      stripe
      style="width: 100%; height: calc(100vh-260px)"
      border
      :header-cell-style="{ background: '#5f7686', color: '#fff' }"
    >
      <el-table-column prop="model" label="型号" align="center">
      </el-table-column>
      <el-table-column prop="factory" label="厂家" align="center">
      </el-table-column>
      <el-table-column
        prop="package_specification"
        label="封装规格"
        align="center"
      >
      </el-table-column>
      <el-table-column prop="year_batch" label="年份批次" align="center">
      </el-table-column>
      <el-table-column prop="in_stock" label="库存" align="center">
      </el-table-column>
      <el-table-column prop="price" label="价格" align="center" width="210">
        <template slot-scope="scope">
          <!-- <div v-for="(pri, index) in scope.row.price" :key="index">
            {{ pri.x + ":  " + pri.y }}
          </div> -->
          <ul class="price-list">
            <li>
              <span class="label">1-20个：</span
              ><span>{{ scope.row.price }}</span>
            </li>
            <li>
              <span class="label">20-100个：</span
              ><span>{{ scope.row.price * 0.9 }}</span>
            </li>
            <li>
              <span class="label">100以上：</span
              ><span>{{ scope.row.price * 0.8 }}</span>
            </li>
          </ul>
        </template>
      </el-table-column>
      <el-table-column prop="delivery_time" label="货期" align="center">
      </el-table-column>
      <el-table-column prop="picture" label="图片" align="center"
        ><template slot-scope="scope">
          <!-- <el-image
            style="width: 100px; height: 100px"
            :src="scope.row.picture"
            :preview-src-list="scope.row.pictures"
            v-if="scope.row.picture != null && scope.row.picture != ''"
          >
          </el-image> -->
          <el-link
            type="primary"
            v-if="scope.row.picture != null && scope.row.picture != ''"
            @click="openImgDialog(scope.row.pictures)"
          >
            图<i class="el-icon-view el-icon--right"></i>
          </el-link>
          <!-- <span v-else> No Data </span> -->
        </template>
      </el-table-column>
      <el-table-column
        :context="_self"
        fixed="right"
        label="操作"
        align="center"
        width="100"
      >
        <template slot-scope="scope">
          <el-popover
            placement="top"
            width="160"
            :ref="`popover-${scope.$index}`"
          >
            数量<el-input-number
              v-model="scope.row.num"
              :min="1"
              size="mini"
              label="描述文字"
            ></el-input-number>
            <div style="text-align: right; margin: 0">
              <el-button size="mini" type="text" @click="pClose(scope.$index)"
                >取消</el-button
              >
              <el-button
                size="mini"
                type="primary"
                @click="addToCart(scope.$index)"
                >确定</el-button
              >
            </div>
            <el-button slot="reference" type="text" size="small"
              >+ 货单</el-button
            >
          </el-popover>
          <el-button
            type="text"
            size="small"
            @click="askForInfomation(scope.row)"
            >咨询</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <!-- 购物车弹窗 -->
    <el-drawer title="我的购物车" :visible.sync="drawer" direction="rtl">
      <div class="cart_item" v-for="(item, index) in cartNumList" :key="index">
        <el-row style="text-align: left">{{ item.model }}</el-row>
        <el-row>
          <el-col :span="12" :offset="0">
            <el-input-number
              v-model="item.num"
              @change="handleChange"
              :min="1"
              size="mini"
              label="描述文字"
            ></el-input-number>
          </el-col>
          <el-col :span="12" :offset="0" style="text-align: right">
            <el-button
              size="mini"
              type="danger"
