<template>
  <div id="app">
    <el-container>
      <el-header>
        <el-row>
          <el-col :span="4" style="text-align: left;">
            <img
              src="./assets/geyun_logo.png"
              height="60px"
              @click="showHomePage()"
            />
          </el-col>

          <!-- <el-col :span="12" :offset="0">
            <el-input
              placeholder="请输入型号"
              v-model="input"
              class="input-search-bar"
            >
              <template v-slot:append>
                <el-button
                  icon="el-icon-search"
                  @click="searchBtnClicked(input)"
