              </template>
            </el-input>
          </el-col> -->

          <el-col v-if="hasLogin === false" :span="4" :offset="16" style="text-align: right;">
            <el-button type="text" @click="loginBtnClicked(true)">登录</el-button>
            <el-button type="text" @click="loginBtnClicked(false)">注册</el-button>
          </el-col>
          <el-col v-else :span="4" :offset="16" style="text-align: right;">
            <i class="el-icon-user"><span style="margin-left:10px">{{ loginEmail }}</span></i>
          </el-col>
        </el-row>
      </el-header>

      <el-main>
        <router-view> </router-view>
      </el-main>

      <el-footer class="footer" style="height: 200px">
        <div style="height: 10 px" />
        <el-row :gutter="20">
          <el-col :span="5">
            <el-row class="footer-row-title">关于我们</el-row>
            <div class="footer-list">关于我们</div>
            <div class="footer-list">联系我们</div>
          </el-col>
          <el-col :span="5"
            ><el-row class="footer-row-title">特色服务</el-row>
            <div class="footer-list">订货服务</div>
            <div class="footer-list">批量采购</div>
            <div class="footer-list">质量保证</div>
          </el-col>
          <el-col :span="5"
            ><el-row class="footer-row-title">购物指南</el-row>
            <div class="footer-list">购物流程</div>
            <div class="footer-list">快递运输</div></el-col
          >
          <el-col :span="5"
            ><el-row class="footer-row-title">售后服务</el-row>
            <div class="footer-list">服务投诉</div>
          </el-col>
          <el-col :span="4" :offset="0">
            <el-row class="footer-row-title">二维码</el-row>
            <div class="footer-qrcode">
              <img src="./assets/qrcode.png" height="50px" />
            </div>
          </el-col>
        </el-row>
      </el-footer>
    </el-container>

    <el-dialog :title="islogin ? '登录' : '注册'" :visible.sync="loginVisible" width="480px">
      <el-form size="small" label-width="80px">
        <el-form-item label="邮箱：" prop="loginEmail" :rules="loginrule">
          <el-input
            placeholder="请输入你的邮箱"
            v-model="loginEmail"
          ></el-input>
        </el-form-item>
        <el-form-item label="密码：">
          <el-input placeholder="请输入密码" v-model="password"></el-input>
        </el-form-item>
      </el-form>
      <template slot="footer" class="dialog-footer">
        <el-button size="mini" @click="loginVisible = false">取 消</el-button>
        <el-button v-if="islogin" size="mini" type="primary" @click="loginWithEmail()"
          >立即登录</el-button
        >
        <el-button v-else size="mini" type="primary" @click="registerWithEmail()"
          >立即注册</el-button
        >
      </template>
    </el-dialog>
  </div>
</template>

<script>
import Constant from "./components/common/Constant.vue";

/* eslint-disable */
export default {
  name: "app",
  components: {
    // HelloWorld
  },
  data() {
    return {
      activeIndex: "1",
      input: "",
      loginVisible: false,
      loginEmail: "",
      password: "",
      hasLogin: false,
      islogin: true,
      loginrule: {
        loginEmail: [
          { required: true, message: '请填写电子邮箱', trigger: 'blur' },
          // { validator: checkEmail, message: '邮箱格式不正确', trigger: 'change'}
        ]
       }
    };
  },
  mounted() {
    if(sessionStorage.getItem('userName')) {
      this.loginEmail = sessionStorage.getItem('userName')
      console.log('username--', this.loginEmail)
      this.hasLogin = true
    }
  },
  methods: {
    checkEmail(rule, value,cb) {
        const regEmail = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
        if (this.loginEmail != '' && !regEmail.test(value)) {
            return cb
        }
        cb(new Error("请输入合法的邮箱"))
    },
    showHomePage() {
      console.log("显示主页");
      if (this.$route.path != "/") {
        this.$router.push("/");
      }
    },
    searchBtnClicked(param) {
      console.log(param);
      console.log("router path:", this.$route.path);
      if (this.$route.path != "/search_result") {
        this.$router.push("/search_result");
      }
    },
    loginBtnClicked(islogin) {
      console.log("loginBtnClicked");
      this.islogin = islogin
      this.loginVisible = true
    },
    loginWithEmail() {
      console.log("loginWithEmail");
      let loginWithEmailApi =
        Constant.SERVER_PRE_URL +
        "/user/login/?email=" +
        this.loginEmail +
        "&password=" +
        this.password;
      console.log("loginWithEmail:", loginWithEmailApi);
      this.$http
        .get(loginWithEmailApi)
        .then((response) => {
          console.log(response.data);
          let json = response.data;
          if (json.errorCode == 0) {
            this.hasLogin = true;
            this.loginVisible = false;
            this.$message.success("登录成功");
            sessionStorage.setItem('isLogin', true)
            sessionStorage.setItem('userName', this.loginEmail)
          } else {
            this.$message.error(json.text);
            this.hasLogin = false
          }
        })
        .catch((error) => {
          this.$message.error("登录失败,code = 2");
          console.log(error);
        });
    },
    registerWithEmail() {
      let registerWithEmailApi =
        Constant.SERVER_PRE_URL +
        "/user/register/?user_email=" +
        this.loginEmail +
        "&password=" +
        this.password;
      console.log("loginWithEmail:", registerWithEmailApi);
      this.$http
        .get(registerWithEmailApi)
        .then((response) => {
          console.log(response.data);
          let json = response.data;
          if (json.errorCode == 0) {
            this.hasLogin = true;
            this.loginVisible = false;
            this.$message.success("登录成功");
          } else {
            this.$message.error(json.text);
          }
        })
        .catch((error) => {
          this.$message.error("注册失败,code = 2");
          console.log(error);
        })
    }
  },
};
</script>



